body {
  max-width: 1920px;
  width: 100%;
  background-color: #fff;
  justify-content: center;
  font-size: 16px;
}

@media screen and (min-width: 480px) {
  .slide {
    width: 100%;
    height: 100%;
    display: block;
  }

  .wrap-container__mob-slide, .container__mob-slide {
    width: 100%;
    height: 100%;
    z-index: 31;
    position: relative;
  }

  .img__slide {
    width: 100%;
    height: 100%;
    display: block;
  }

  .wrap__mob-slide {
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .wrap__slide-up, .wrap__slide-down {
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
  }

  .mob__slide {
    flex: 1;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .mob__img-slide {
    max-width: 100%;
    max-height: 100%;
  }

  .mob__slide-up, .mob__slide-down {
    width: 300%;
    height: 100%;
    flex-direction: row;
    transition: transform 12s linear;
    animation: animationMobSlide 12s linear infinite alternate;
    display: flex;
    position: relative;
  }

  @keyframes animationMobSlide {
    0%, 100% {
      transform: translateX(0%);
    }

    16.6667% {
      transform: translateX(0%);
    }

    25% {
      transform: translateX(-33%);
    }

    41.6667% {
      transform: translateX(-33%);
    }

    50% {
      transform: translateX(-66.7%);
    }

    66.6667% {
      transform: translateX(-66.7%);
    }
  }

  .mob__slide {
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
  }

  .img-slide__mob {
    width: 100%;
    height: 100%;
    display: inline-block;
  }

  .swiper__hero {
    z-index: 30;
    height: 820px;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    overflow: hidden;
  }

  .img-slide__desc {
    display: none;
  }
}

@media (min-width: 768px) {
  .img-slide__mob {
    max-width: 300%;
    height: auto;
  }

  .mob__slide-up, .mob__slide-down {
    width: 300%;
    height: 100%;
  }

  .img__slide {
    width: 100%;
    height: 100%;
    display: block;
  }

  .mob__img-slide {
    width: 100%;
  }
}

@media screen and (min-width: 1200px) {
  .wrap-container__mob-slide, .img-slide__mob {
    display: none;
  }

  .wrap-container__right-slide, .wrap-container__left-slide {
    transition: transform 2s;
  }

  .img-slide__desc {
    width: 100%;
    height: 100%;
    display: block;
  }

  .img-slide__mob {
    display: none;
  }

  .wrap__slide {
    width: 100%;
    height: 100%;
    z-index: 31;
    position: absolute;
  }

  .wrap-slide__container {
    width: 100%;
    height: 100%;
    justify-content: space-between;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .wrap-slide__container.element.style {
    display: block;
  }

  .wrap__left-slide {
    width: 100%;
    flex-direction: column;
    margin: 0;
    transition: transform 20s;
    animation: animationLeft 16s linear infinite alternate;
    display: flex;
    transform: translateY(0%);
  }

  @keyframes animationLeft {
    0%, 100% {
      transform: translateY(0%);
    }

    16.6667% {
      transform: translateY(0%);
    }

    25% {
      transform: translateY(-35%);
    }

    41.6667% {
      transform: translateY(-35%);
    }

    50% {
      transform: translateY(-66.7%);
    }

    66.6667% {
      transform: translateY(-66.7%);
    }
  }

  .swiper-slide__left {
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
  }

  .swiper-slide__left :nth-child(1), .swiper-slide__left :nth-child(2), .swiper-slide__left:nth-child(3) {
    animation-delay: 1s;
  }

  .swiper-slide__right {
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
  }

  .wrap__right-slide {
    width: 100%;
    height: 300%;
    flex-direction: column;
    margin: 0;
    transition: transform 20s;
    animation: animationRight 16s linear infinite alternate forwards;
    display: flex;
    position: relative;
    transform: translateY(0%);
  }

  @keyframes animationRight {
    0%, 100% {
      transform: translateY(-66.7%);
    }

    16.6667% {
      transform: translateY(-66.7%);
    }

    25% {
      transform: translateY(-35%);
    }

    41.6667% {
      transform: translateY(-35%);
    }

    50% {
      transform: translateY(0%);
    }

    66.6667% {
      transform: translateY(0%);
    }
  }

  .wrap__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: relative;
  }

  .button__menu-open:focus animationLeft, animationLeft {
    animation-play-state: paused;
  }

  .title__right {
    max-width: 1920px;
    width: 100%;
    height: 100%;
    color: #fff;
    text-transform: uppercase;
    background-color: rgba(47, 48, 58, .4);
    padding-top: 300px;
    padding-left: 0%;
    font-family: Fiver;
    font-size: 100px;
    font-weight: 400;
    line-height: .5;
    position: absolute;
    top: 0%;
  }

  .title__left {
    width: 100%;
    height: 100%;
    color: #fff;
    text-transform: uppercase;
    text-align: right;
    background-color: rgba(47, 48, 58, .4);
    padding-top: 300px;
    font-family: Fiver;
    font-size: 100px;
    font-weight: 400;
    line-height: .5;
    position: absolute;
    top: 0%;
  }

  .img__left-slide, .img-slide__desc {
    position: relative;
  }

  .logo {
    z-index: 3;
    position: absolute;
    top: 20px;
    left: 20px;
  }
}

@media screen and (min-width: 1400px) {
  .title__right, .title__left {
    font-size: 130px;
  }
}

